'use client';

import { Button, Label, TextInput, Textarea, Select } from 'flowbite-react';
import { LocateIcon, Mail, PhoneCall } from 'lucide-react';
import Link from 'next/link';
const addresses = [
  {
    title: 'Zug – Headquarters',
    company: 'Evalueserve Holdings AG',
    address: ['Bahnhofstrasse 10 6300,', 'Zug,', 'Switzerland'],
  },
  {
    title: 'Bengaluru',
    company: 'Evalueserve.com Pvt. Ltd',
    address: ['2nd Floor, SJR 1 Park, Road Number 9 EPIP Zone, Whitefield', 'Bengaluru – 560066,', 'Karnataka,', 'India'],
  },
  {
    title: 'Cairo',
    company: 'Evalueserve',
    address: ['Office 521 Regus Cairo, Maadi Club 5th Floor, Building No 55, Road 18,', 'Sarayat El Maadi, Cairo,', 'Egypt'],
  },
];
export default function ContactPage() {
  return (
    <>
      <section className="bg-slate-50 py-16 px-6">
        <div className="mx-auto max-w-7xl grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
          <div>
            <h1 className="text-4xl font-extrabold  mb-4">
              <span className="text-secondary">Connect</span> with Our <br /> Team of Experts
            </h1>

            <p className="text-slate-600 max-w-md mb-8">Contact our team of excellence-driven experts today to bring your project to life.</p>

            <div className="space-y-4 mb-10">
              <div className="flex items-center gap-3 text-slate-700">
                <PhoneCall className="text-blue-600" />
                <span>+91 7856450005</span>
              </div>

              <div className="flex items-center gap-3 text-slate-700">
                <Mail className="text-blue-600" />
                <span>info@dolcera.com</span>
              </div>

              <div className="flex items-center gap-3 text-slate-700">
                <LocateIcon className="text-blue-600" />
                <span>See Our Locations</span>
              </div>
            </div>

            <div>
              <h3 className="text-lg font-semibold text-slate-900">Want to Join Our Talented Team?</h3>
              <a href="#" className="text-blue-600 font-medium hover:underline">
                Visit Our Job Board →
              </a>
            </div>
          </div>

          <div className="bg-gradient-to-br from-blue-900 to-blue-800 rounded-xl p-8 text-white shadow-lg">
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              <div>
                <TextInput id="name" placeholder="Full Name" required />
              </div>

              <div>
                <TextInput id="email" type="email" placeholder="Email Address" required />
              </div>
            </div>
            <div className="grid grid-cols-1 sm:grid-cols-1 gap-4">
              <div>
                <Label htmlFor="name" className="text-white">
                  Tell Us About Your Project
                </Label>
                <TextInput id="name" placeholder="Subject" required />
              </div>

              <div>
                <Textarea id="message" placeholder="Leave your message here" rows={4} />
              </div>
            </div>
          </div>

          <div></div>

          <Button color="light" className="w-full">
            Submit →
          </Button>
        </div>
      </section>
      <section className="bg-white py-16 px-6">
        <div className="max-w-7xl mx-auto grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {addresses.map((item, index) => (
            <div key={index} className="border border-gray-200 rounded-sm p-6 hover:shadow-md transition">
              <h3 className="text-xl font-semibold text-purple-900 mb-3">{item.title}</h3>

              <p className="text-gray-800 font-medium mb-1">{item.company}</p>

              <p className="text-gray-600 text-sm leading-relaxed mb-6">
                {item.address.map((line, i) => (
                  <span key={i} className="block">
                    {line}
                  </span>
                ))}
              </p>

              <Link href="#" className="text-purple-700 font-medium inline-flex items-center gap-1 hover:underline">
                See on Map <span>›</span>
              </Link>
            </div>
          ))}
        </div>
      </section>
    </>
  );
}
